.menu-card {
  position: relative;
  width: 100%;
  max-width: 800px;
  max-height: 300px; /* Höhe begrenzen, um Overflow sichtbar zu machen */
  padding: 0px;
  background: var(--tab-bg-color);
  color: var(--tab-text-color);
  border-radius: 20px;
  box-shadow: 8px 8px 16px var(--shadow-dark), -8px -8px 16px var(--shadow-light);
  overflow-y: hidden; /* Verhindert, dass der Inhalt über die Card hinausgeht */
  margin: 0 auto;
  margin-top: 50px;
}

.scrollable-content {
  z-index: 13;
  position: relative;
  display: block;
  max-height: 1000px; /* Ändere dies nach Bedarf, um sicherzustellen, dass der Inhalt scrollbar ist */
  width: 95%;
  overflow: auto;
  padding: 20px;
  border-radius: 10px;
  background: var(--tab-bg-color);
  color: var(--tab-text-color);
  box-shadow: 8px 8px 16px var(--shadow-dark), -8px -8px 16px var(--shadow-light);
}

.scrollable-content::-webkit-scrollbar {
  width: 10px;
}

.scrollable-content::-webkit-scrollbar-thumb {
  background: var(--tab-bg-color);
  border-radius: 10px;
}

.scrollable-content::-webkit-scrollbar-track {
  background: var(--tab-bg-color);
  border-radius: 10px;
}

.category-slide {
  margin-bottom: 20px;
}

.category-slide h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

.category-slide p {
  font-size: 16px;
  line-height: 1.5;
}

/* Light Mode */
.light-mode {
  --tab-bg-color: #e0e0e0;
  --tab-bg-hover: #f0f0f0;
  --shadow-dark: #aaaaaa;
  --shadow-light: #ffffff;
  --tab-text-color: #333333;
}

/* Dark Mode */
.dark-mode {
  --tab-bg-color: #333333;
  --tab-bg-hover: #444444;
  --shadow-dark: #000000;
  --shadow-light: #444444;
  --tab-text-color: #f0f0f0;
}

/* Media Queries */
@media screen and (min-width: 376px) and (max-width: 600px) {
  .menu-card {
    margin-top: 0px;
  }
}

@media screen and (min-width: 601px) and (max-width: 1024px) {
  .menu-card {
    margin-top: 50px;
  }
}

@media screen and (min-width: 1025px) {
  .menu-card {
    margin-top: 50px;
  }
}
